function Services() {
    useEffect(()=>{
        window.scrollTo(0,0)
    },[])
      return(
          <Layout>
            <Helmet>
              <title>Sky | Servicios</title>
              <meta name="description" content="Agencia de software y marketing digital. Servicios de creacion de pagina web y desarrollo de aplicaciones." />
              <meta name="keywords" content='agencia de software, agencia de marketing, creacion de pagina web' />
              <meta name="robots" content='all' />
              <link rel="canonical" href="https://www.sky.com/" />
              <meta name="author" content='Murkiva' />
              <meta name="publisher" content='Murkiva' />
  
              {/* Social Media Tags */}
              <meta property="og:title" content='Sky | Agencia de Software' />
              <meta property="og:description" content='Agencia de software y marketing digital. Servicios de creacion de pagina web y desarrollo de aplicaciones.' />
              <meta property="og:url" content="https://www.sky.com/" />
              <meta property="og:image" content='https://bafybeicwrhxloesdlojn3bxyjqnxgsagtd4sl53a7t4cn4vfe2abmybzua.ipfs.w3s.link/lightbnuilbg.jpg' />
  
              <meta name="twitter:title" content='Sky | Agencia de Software' />
              <meta
                  name="twitter:description"
                  content='Agencia de software y marketing digital. Servicios de creacion de pagina web y desarrollo de aplicaciones.'
              />
              <meta name="twitter:image" content='https://bafybeicwrhxloesdlojn3bxyjqnxgsagtd4sl53a7t4cn4vfe2abmybzua.ipfs.w3s.link/lightbnuilbg.jpg' />
              <meta name="twitter:card" content="summary_large_image" />
          </Helmet>
              <Navbar/>
              <div className="pt-28">
                  <Header/>
                  <div className="py-12 bg-gray-50">
  
                  </div>
                  <ServicesList posts={posts_software} section_title={'Servicio de Desarrollo de Software'}/>
                  <ServicesList posts={posts_design} section_title={'Servicios de Diseño'}/>
              </div>
                  <Footer/>
          </Layout>
      )
  }


export default Services